;(function(global) {
    /**
     * 
     * @namespace wol
     */
    global.wol = global.wol || {};

    /**
     * 
     * @namespace wol.util
     */
    global.wol.util = global.wol.util || {};

    /**
     * 
     * @property {ol.style.Style} DEFAULT_STYLE - 
     * @property {ol.style.Style} TEXT_STYLE - 
     * @property {ol.style.Style} MASK_BLACK - 
     */
    wol.util.styles = {
        DEFAULT_STYLE: new ol.style.Style({
            fill: new ol.style.Fill({
                color : 'rgba(0, 191, 255, 0.5)'
            }),
            image: new ol.style.Circle({
                fill: new ol.style.Fill({
                    color : '#00bfff'
                }),
                stroke: new ol.style.Stroke({
                    color: '#bdecff',
                    width: 2
                }),
                radius: 8
            }),
            stroke: new ol.style.Stroke({
                color: '#00bfff',
                width: 3
            })
        }),
        TEXT_STYLE: new ol.style.Style({
            text: new ol.style.Text({
                text: '',
                offsetY: 18,
                font: '14px ',
                textAlign : 'center',
                fill: new ol.style.Fill({
                    color : '#2d2d2d',
                })
            })
        }),
        MASK_BLACK: new ol.style.Style({
            fill : new ol.style.Fill({
                color : 'rgba(0, 0, 0, 0.5)'
            })
        })
    };

    /**
     * 
     * @param {object} option - 
     * @param {string|undefined} option.name -  undefined
     * @param {string} option.label -  ''
     * @param {ol.style.Style} option.style -  wol.util.styles.DEFAULT_STYLE
     * @param {Array} option.features -  []
     * @param {boolean} option.updateWhileAnimating -  true
     * @param {boolean} option.updateWhileInteracting -  false
     * @param {boolean} option.useSpatialIndex -  false
     * @param {number} option.opacity -  1
     * @param {boolean} option.visible -  true
     * @param {number|undefined} option.zIndex -  undefined
     * @param {string|undefined} option.group -  undefined 
     * @return {ol.layer.Vector} - 
     */
    wol.util.createVectorLayer = function(option) {
        var defaults = {
            name: undefined,
            label: '',
            style: wol.util.styles.DEFAULT_STYLE,
            features: [],
            updateWhileAnimating: true,
            updateWhileInteracting: false,
            useSpatialIndex: false,
            opacity: 1,
            visible: true,
            zIndex: undefined,
            group: undefined
        };
        option = wol.util.extend(defaults, option);

        var layer = new ol.layer.Vector({
            source: new ol.source.Vector({
                features: option.features,
                useSpatialIndex: option.useSpatialIndex
            }),
            style: option.style,
            updateWhileAnimating: option.updateWhileAnimating,
            updateWhileInteracting: option.updateWhileInteracting,
            opacity: option.opacity,
            visible: option.visible,
            zIndex: option.zIndex
        });
        layer.set('name', option.name);
        layer.set('label', option.label);
        layer.set('group', option.group);
        layer.set('type', 'Vector');

        return layer;
    }

    /**
     * XYZ
     * @param {object} option - 
     * @param {string} option.name -  undefined
     * @param {string} option.label -  ''
     * @param {string} option.url - 
     * @param {string|undefined} option.crossOrigin - 'anonymous'  undefined undefined'anonymous'
     * @param {number|undefined} option.minZoom -  undefined
     * @param {number|undefined} option.maxZoom -  undefined
     * @param {number} option.opacity -  1
     * @param {boolean} option.visible -  true
     * @param {number} option.zIndex -  undefined
     * @param {string|undefined} option.group -  undefined 
     * @return {ol.layer.Tile} - 
     */
    wol.util.createXYZLayer = function(option) {
        var defaults = {
            name: undefined,
            label: '',
            url: '',
            crossOrigin: undefined,
            minZoom: undefined,
            maxZoom: undefined,
            opacity: 1,
            visible: true,
            zIndex: undefined,
            group: undefined
        };
        option = wol.util.extend(defaults, option);

        var layer = new ol.layer.Tile({
            source : new ol.source.XYZ({
                url : option.url,
                crossOrigin: option.crossOrigin,
                wrapX: option.wrapX,
                minZoom: option.minZoom,
                maxZoom: option.maxZoom
            }),
            opacity: option.opacity,
            visible: option.visible,
            zIndex: option.zIndex
        });
        layer.set('name', option.name);
        layer.set('label', option.label);
        layer.set('group', option.group);
        layer.set('type', 'XYZ');

        return layer;
    }

    /**
     * 
     * @param {object} option - 
     * @param {string} option.name -  undefined
     * @param {string} option.label -  ''
     * @param {string} option.url - 
     * @param {object} option.params - WMS LAYERS
     * @param {string|undefined} option.serverType - 'carmentaserver', 'geoserver', 'mapserver', 'qgis' undefined
     * @param {number} option.maxZoom - 
     * @param {number} option.opacity -  1
     * @param {boolean} option.visible -  true
     * @param {number} option.zIndex -  undefined
     * @param {string|undefined} option.group -  undefined 
     * @return {ol.layer.Tile} - WMS
     */
    wol.util.createWMSLayer = function(option) {
        var defaults = {
            name: undefined,
            label: '',
            url: '',
            params: {},
            serverType: undefined,
            maxZoom: undefined,
            opacity: 1,
            visible: true,
            zIndex: undefined,
            group: undefined
        };
        option = wol.util.extend(defaults, option);

        var layer = new ol.layer.Tile({
            source: new ol.source.TileWMS({
                url: option.url,
                params: option.params,
                serverType: option.serverType,
                wrapX: option.wrapX,
                maxZoom: option.maxZoom
            }),
            opacity: option.opacity,
            visible: option.visible,
            zIndex: option.zIndex
        });
        layer.set('name', option.name);
        layer.set('label', option.label);
        layer.set('group', option.group);
        layer.set('type', 'WMS');

        return layer;
    }

    /**
     * 
     * @param {object} option - 
     * @param {string} option.name -  undefined
     * @param {string} option.label -  ''
     * @param {ol.style.Style} option.style -  wol.util.styles.MASK_BLACK
     * @param {boolean} option.updateWhileAnimating -  true
     * @param {boolean} option.updateWhileInteracting -  true
     * @param {number} option.opacity -  1
     * @param {boolean} option.visible -  false
     * @param {number} option.zIndex -  1
     * @param {string|undefined} option.group -  undefined 
     * @return {ol.layer.Vector} - 
     */
    wol.util.createMaskLayer = function(option) {
        //
        var maskFeature = wol.util.createFeatureFromWKT('POLYGON((115.282079820752 41.0624818532467,115.282079820752 36.6007452585101,119.034581570745 36.6007452585101,119.034581570745 41.0624818532467,115.282079820752 41.0624818532467))');
        wol.util.transform(maskFeature, 'EPSG:4326', 'EPSG:3857');

        var defaults = {
            name: undefined,
            label: '',
            features: [maskFeature],
            style: wol.util.styles.MASK_BLACK,
            updateWhileAnimating: true,
            updateWhileInteracting: true,
            opacity: 1,
            visible: false,
            zIndex: 1,
            group: undefined
        };
        option = wol.util.extend(defaults, option);

        var maskLayer = wol.util.createVectorLayer(option);
        maskLayer.set('name', option.name);
        maskLayer.set('label', option.label);
        maskLayer.set('group', option.group);
        maskLayer.set('type', 'Mask');

        return maskLayer;
    }

    /**
     * 
     * @param {object} option - 
     * @param {string|undefined} option.name -  undefined
     * @param {string} option.label -  ''
     * @param {Array} option.feature -  []
     * @param {Array} option.gradient -  ['#0ff', '#00f', '#0f0', '#ff0', '#ff5d3e']
     * @param {number} option.blur -  24
     * @param {number} option.radius -  10
     * @param {number} option.shadow -  300
     * @param {number} option.opacity -  1
     * @param {boolean} option.visible -  true
     * @param {number|undefined} option.zIndex -  undefined
     * @param {string|undefined} option.group -  undefined 
     * @return {ol.layer.Heatmap} - 
     */
    wol.util.createHeatMap = function(option) {
        var defaults = {
            name: undefined,
            label: '',
            feature: [],
            gradient : ['#0ff', '#00f', '#0f0', '#ff0', '#ff5d3e'],
            blur : 24,
            radius : 10,
            shadow : 300,
            opacity: 1,
            visible: true,
            zIndex: undefined,
            group: undefined,
        };
        option = wol.util.extend(defaults, option);

        var layer = new ol.layer.Heatmap({
            source: new ol.source.Vector({
                features : option.features
            }),
            gradient: option.gradient,
            blur: option.blur,
            radius: option.radius,
            shadow: option.shadow,
            opacity: option.opacity,
            visible: option.visible,
            zIndex: option.zIndex
        });
        layer.set('name', option.name);
        layer.set('label', option.label);
        layer.set('group', option.group);
        layer.set('type', 'HeatMap');

        return layer;
    }

    /**
     * WKT
     * @param {string} wkt - wktwkt{@link http://www.cnblogs.com/tiandi/archive/2012/07/18/2598093.html}
     * @return {ol.Feature} - 
     */
    wol.util.createFeatureFromWKT = function(wkt) {
        var format = new ol.format.WKT();
        var feature = format.readFeature(wkt);
        return feature;
    }

    /**
     * WKT
     * @param {ol.Feature} feature - 
     * @return {string} - wkt
     */
    wol.util.createWKTFromFeature = function(feature) {
        var format = new ol.format.WKT();
        var wkt = format.writeFeature(feature);
        return wkt;
    }

    /**
     * 
     * @param {ol.Feature} feature - 
     * @param {ol.proj.ProjectionLike} from - 
     * @param {ol.proj.ProjectionLike} to - 
     */
    wol.util.transform = function(feature, from, to) {
        feature.getGeometry().transform(from, to);
    }

    /**
     * ol.Extent
     * @param {*} arg0 - -
     * @return {boolean} - true/false
     */
    wol.util.isExtent = function(arg0) {
        if((arg0 instanceof Array) && arg0.length == 4) {
            return true;
        }else {
            return false;
        }
    }

    /**
     * jQuery extend
     * @param {boolean|object} arg - 
     *  false 
     * @return {object} - 
     */
    wol.util.extend = (function fn() {
        var options, name, src, copy, copyIsArray, clone,
            target = arguments[0] || {},
            length = arguments.length,
            i = 1, deep = false;

        //
        if(typeof target === "boolean") {
            deep = target;
            target = arguments[ i ] || {};
            i++;
        }

        if(typeof target !== "object" && !jQuery.isFunction(target)) {
            target = {};
        }

        if(i === length) {
            target = {};
            i--;
        }

        for(; i < length; i++) {
            if((options = arguments[i]) != null) {
                for(name in options) {
                    src = target[name];
                    copy = options[name];

                    // Prevent never-ending loop
                    if(target === copy) {
                        continue;
                    }

                    // Recurse if we're merging plain objects or arrays
                    if(deep && copy && (jQuery.isPlainObject(copy) || (copyIsArray = jQuery.isArray(copy)))) {
                        if(copyIsArray) {
                            copyIsArray = false;
                            clone = src && jQuery.isArray(src) ? src : [];
                        }else {
                            clone = src && jQuery.isPlainObject(src) ? src : {};
                        }

                        // Never move original objects, clone them
                        target[name] = fn(deep, clone, copy);
                    }else if (copy !== undefined) {
                        target[name] = copy;
                    }
                }
            }
        }

        // Return the modified object
        return target;
    });

    /**
     * 
     * @param {string} colorStr - 
     * @return {string} - RGB/
     */
    wol.util.getColorType = function(colorStr) {
        //
        var rgbColorReg = /^(rgb[(]|RGB[(]).+([)])$/;
        var hexColorReg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;

        if(rgbColorReg.test(colorStr)) {
            return 'RGB';
        }else if(hexColorReg.test(colorStr)) {
            return 'HEX';
        }else {
            return 'UNKNOWN';
        }
    }

    /**
     * rgb16
     * @param {string} rgbStr - RGB
     * @return {string} - 16
     */
    wol.util.toHexColor = function(rgbStr) {
        var colorType = wol.util.getColorType(rgbStr);
        if(colorType == 'RGB') {
            var aColor = rgbStr.replace(/(?:\(|\)|rgb|RGB)*/g, '').split(',');
            var hexStr = '#';
            for(var i = 0; i<aColor.length; i++) {
                var hex = Number(aColor[i]).toString(16);
                if(hex === '0'){
                    hex += hex;
                }
                hexStr += hex;
            }
            if(hexStr.length !== 7){
                hexStr = '#000000';
            }
            return hexStr;
        }else if(colorType == 'HEX') {
            var aNum = rgbStr.replace(/#/, '').split('');
            if(aNum.length === 6) {
                return rgbStr;
            }else if(aNum.length === 3) {
                var numHex = '#';
                for(var i=0; i<aNum.length; i+=1){
                    numHex += (aNum[i]+aNum[i]);
                }
                return numHex;
            }
        }else {
            return '#000000';
        }
    }

    /**
     * 16rgb
     * @param {string} hexStr - 16
     * @return {string} - RGB
     */
    wol.util.toRGBColor = function(hexStr) {
        var sColor = hexStr.toLowerCase();
        var colorType = wol.util.getColorType(sColor);
        if(sColor && colorType == 'HEX') {
            if(sColor.length === 4){
                var sColorNew = '#';
                for(var i = 1; i < 4; i+=1) {
                    sColorNew += sColor.slice(i,i+1).concat(sColor.slice(i,i+1));
                }
                sColor = sColorNew;
            }
            //
            var sColorChange = [];
            for(var i = 1; i < 7; i+=2) {
                sColorChange.push(parseInt('0x' + sColor.slice(i,i+2)));
            }
            return 'RGB(' + sColorChange.join(',') + ')';
        }else{
            return 'RGB(0, 0, 0)';
        }
    }

    /**
     * 
     * @param {string} color - 
     * @return {Array} - RGB
     */
    wol.util.getColorArray = function(color) {
        var type = wol.util.getColorType(color);
        if(type == 'HEX') {
            color = wol.util.toRGBColor(color);
        }else if(type != 'RGB') {
            color = 'RGB(0, 0, 0)';
        }

        var temp = color.replace(/(?:\(|\)|rgb|RGB)*/g, '').split(',');
        for(var i = 0; i < temp.length; i++) {
            temp[i] = parseInt(temp[i]);
        }
        return temp;
    }
})(window);